import React from 'react';
import styles from './Projects.module.css'
import ProjectsItem from "./Item/ProjectsItem";
import BlockTitle from "../BlockTitle";
import todo from '../../assets/projects/todo.png'
import books from '../../assets/projects/books.png'
import pizzas from '../../assets/projects/pizzas.png'
import counter from '../../assets/projects/counter.png'
import duck from '../../assets/projects/duck-hunt.png'
import {particleOpt} from "../../App";
import Particles from "react-particles-js";


function Projects() {

    const arr = [
        {
            title:'ToDo',
            descr: 'ReactJS,Redux,Redux-Thunk,TypeScript,SCSS,React-Router,REST-API',
            urlDemo: 'https://zionjke.github.io/toDoList',
            projectBack: todo
        },
        {
            title:'Books-magazine',
            descr: 'ReactJS,Redux,Redux-Thunk,Semantic-UI,REST-API',
            urlDemo: 'https://zionjke.github.io/book-magazine/',
            projectBack: books
        },
        {
            title:'Pizzas-magazine',
            descr: 'ReactJS,Redux,Redux-Thunk,SCSS,REST-API,React Hooks,React-Router',
            urlDemo: 'https://zionjke.github.io/pizza-magazine',
            projectBack: pizzas
        },
        {
            title:'Counter',
            descr: '',
            urlDemo: 'https://zionjke.github.io/Counter/',
            projectBack: counter
        },
        {
            title:'Duck Hunt',
            descr: '',
            urlDemo: 'https://zionjke.github.io/game_dog/',
            projectBack: duck
        },


    ]

  return (
      <div id='projects' className={styles.projects}>
          <Particles className='particles' params={particleOpt}/>
          <div className={styles.container}>
                  <BlockTitle title='Personal Projects'/>
              <div className={styles.myprojects_items}>
                  {arr.map((item,index)=><ProjectsItem key={index} {...item}/>)}
              </div>
          </div>
      </div>
  );
}

export default Projects;
